:root {
    --primary-color: #4A90E2;
    --secondary-color: #50E3C2;
    --background-color: #F5F7FA;
    --text-color: #333333;
    --border-color: #E0E0E0;
}

body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background-color: var(--background-color);
    color: var(--text-color);
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

button {
    cursor: pointer;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: var(--primary-color);
    color: white;
}

input, textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid var(--border-color);
    border-radius: 5px;
    margin-bottom: 10px;
}

.task-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    border: 1px solid var(--border-color);
    border-radius: 5px;
    margin-bottom: 10px;
    background-color: white;
}

.task-item.completed {
    text-decoration: line-through;
    background-color: #E0E0E0;
}

.search-bar {
    margin-bottom: 20px;
}

.category-filter {
    margin-bottom: 20px;
}